import $http from './apiHelper'
import qs from 'qs'
import axios from 'axios'
import { decodeArrayBuffer } from '@/utils/uitilFuntions'

const createUrl = (url) => {
  let code = 'G0JQzK7qoEKnPD8v1esiGhpVUigfOVqUggRfd6wCDXYrAzFu41ZG-w=='
  let parameters = qs.stringify({
    code: code,
  })
  return url + '?' + parameters
}

/**
 * getOrderById
 * 取得報價單ById
 * @param {*} orderId
 * @returns
 */

const getOrderById = (orderId) => {
  let request = {
    body: {
      orderId: orderId,
    },
  }
  let url = createUrl('api/getOrderById')

  return $http.post(url, request.body)
}
