const getProductJson = async () => {
  const arrayBuffer = await $http.get(
    'https://ebudatadev.blob.core.windows.net/lspoutput/Faculty-21.json.gz',
    {
      responseType: 'arraybuffer',
    },
  )

  return decodeArrayBuffer(arrayBuffer)
}

const getCategoryByVersion = async (category, veriosn) => {
  const arrayBuffer = await $http.get(
    `https://ebudatadev.blob.core.windows.net/lspoutput/${category}-${veriosn}.json.gz`,
    {
      responseType: 'arraybuffer',
    },
  )

  return decodeArrayBuffer(arrayBuffer)
}
/**
 * uploadProductFile
 * 搜尋報價單
 * @param {*} company: string;
 * @param {*} createdDate: string;
 * @returns
 */

const uploadProductFile = (params) => {
  let request = {
    body: params,
    headers: {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    },
  }
  let url = createUrl('api/uploadProductFile')

  return $http.post(url, request.body, request.headers)
}
