import { useState } from 'react'

const useApi = (apiFunction) => {
  const [data, setData] = useState(null)
  const [error, setError] = useState('')
  const [loading, setLoading] = useState(false)

  const request = async (...args) => {
    setLoading(true)
    setError('')
    try {
      const response = await apiFunction(...args)
      console.log('response: ', JSON.stringify(response))
      console.log('apiFuntions: ', apiFunction?.name)
      setData(response)
      return response
    } catch (e) {
      const errorMsg = `ReactNativeError: args: ${args} ${e.message} `
      console.error(errorMsg)
      setError(e.message || 'API request Unexpected Error!')
    } finally {
      setLoading(false)
    }
  }

  const resetError = () => {
    setError('')
  }

  const resetData = () => {
    setData(null)
  }

  return {
    data,
    error,
    loading,
    request,
    resetError,
    resetData,
  }
}
export default useApi
