import React, { useState, useEffect } from 'react'
import { CContainer, CRow, CCol, CFormInput, CButton } from '@coreui/react'
import Pagenate from '@views/Share/Pagenate'
import { getOrderReports } from '@service/api'
import useApi from '@service/useApi'
import { priceFormat } from '@utils/uitilFuntions'
import Loader from '@views/Share/Loader'

const renderApiStatusText = (text) => {
  return <div className="d-flex justify-content-center p-5">{text}</div>
}

const QuotationReport = () => {
  const { request, data, loading, error } = useApi(getOrderReports)
  const [currentPage, setCurrentPage] = useState(0)
  const [totalPages, setTotalPages] = useState(1)
  const [filterData, setFilterData] = useState({
    company: '',
    createdDate: '',
  })
  const itemsPerPage = 10

  useEffect(() => {
    if (data && data.status.toString() === '200') {
      setTotalPages(Math.ceil(data.data.reports.length / itemsPerPage))
    }
  }, [data])

  const startIndex = currentPage * itemsPerPage
  const endIndex = startIndex + itemsPerPage
  const subset = data?.data?.reports?.slice(startIndex, endIndex)
  console.log(data?.data?.reports, currentPage)
  const handlePageChange = (selectedPage) => {
    setCurrentPage(selectedPage.selected)
  }

  const renderContentLists = () => {
    return (
      <div className="list-body">
        {subset && subset.length > 0
          ? subset.map((item, i) => {
              return (
                <div className="list-item" key={item.num + 'list' + i}>
                  <CRow>
                    <CCol sm={1}>{item.company}</CCol>
                    <CCol sm={2}>{item.orderId}</CCol>
                    <CCol sm={1}>{item.partNum}</CCol>
                    <CCol className="ellipsis" sm={1}>
                      {item.productDes}
                    </CCol>
                    <CCol sm={1}>{item.startDate}</CCol>
                    <CCol sm={1}>{item.measure}</CCol>
                    <CCol sm={1}>{item.unitPrice}</CCol>
                    <CCol sm={1}>{item.QTY}</CCol>
                    <CCol sm={1}>{priceFormat(item.totalAmount)}</CCol>
                    <CCol sm={1}>{priceFormat(item.CPSprice)}</CCol>
                    <CCol sm={1}>{priceFormat(item.costTotal)}</CCol>
                  </CRow>
                </div>
              )
            })
          : renderApiStatusText('無資料')}
      </div>
    )
  }

  const onChangeInfoValue = (name, value) => {
    filterData[name] = value
    setFilterData(filterData)
  }
  const handleSearch = () => {
    request(filterData)
    setCurrentPage(0)
  }

  if (error) return renderApiStatusText('系統忙碌中，請稍後再試。')

  return (
    <div className="lists-container px-3">
      <CContainer>
        <div className="form-box">
          <CRow className="mb-3">
            <CCol md={3} sm={4}>
              <CFormInput
                label="客戶名稱"
                placeholder="請輸入客戶名稱"
                defaultValue={filterData.company}
                onChange={(e) => onChangeInfoValue('company', e.target.value)}
              />
            </CCol>
            <CCol md={3} sm={4}>
              <CFormInput
                type="date"
                label="起始日期"
                placeholder="請輸入日期"
                defaultValue={filterData.createdDate}
                onChange={(e) => onChangeInfoValue('createdDate', e.target.value)}
              />
            </CCol>

            <CCol md={2} sm={4} className="d-flex align-items-end">
              <CButton onClick={() => handleSearch()} color="primary">
                搜尋
              </CButton>
            </CCol>
          </CRow>
        </div>
        <div className="list-head">
          <CRow>
            <CCol sm={1}>客戶名稱</CCol>
            <CCol sm={2}>訂單編號</CCol>
            <CCol sm={1}>Part Number</CCol>
            <CCol sm={1}>Product Description</CCol>
            <CCol sm={1}>Usage start Date</CCol>
            <CCol sm={1}>Unit of Measure</CCol>
            <CCol sm={1}>Unit of Price</CCol>
            <CCol sm={1}>License Qty</CCol>
            <CCol sm={1}>Total Amount</CCol>
            <CCol sm={1}>MS CPS Price</CCol>
            <CCol sm={1}>MS Cost Total</CCol>
          </CRow>
        </div>
        {loading ? (
          <Loader isShow />
        ) : (
          <>
            {renderContentLists()}
            {data?.data?.reports?.length > itemsPerPage && (
              <Pagenate
                totalPages={totalPages}
                currentPage={currentPage}
                handlePageChange={handlePageChange}
              />
            )}
          </>
        )}
      </CContainer>
    </div>
  )
}

export default QuotationReport
